import { createRoot } from 'react-dom/client';
import LiveChatWidget from './widget/LiveChatWidget';
import './widget.css';

// Widget initialization function
const initLiveChatWidget = (config: any = {}) => {
  const container = document.createElement('div');
  container.id = 'live-chat-widget-container';
  document.body.appendChild(container);

  const root = createRoot(container);
  root.render(<LiveChatWidget {...config} />);
};

// Auto-initialize if data attributes are present
document.addEventListener('DOMContentLoaded', () => {
  const script = document.currentScript || document.querySelector('script[data-widget]');
  if (script) {
    const config = {
      apiUrl: script.getAttribute('data-api-url'),
      socketUrl: script.getAttribute('data-socket-url'),
      tenantUrl: script.getAttribute('data-tenant-url'),
    };
    initLiveChatWidget(config);
  }
});

// Export for UMD build
export { initLiveChatWidget };

// Also attach to window for global access
if (typeof window !== 'undefined') {
  (window as any).initLiveChatWidget = initLiveChatWidget;
} 