import React, { useState } from 'react';
import ChatWidget from './components/ChatWidget';
import Content from './components/Content';

interface LiveChatWidgetProps {
  apiUrl?: string;
  socketUrl?: string;
  tenantUrl?: string;
  position?: 'bottom-right' | 'bottom-left' | 'top-right' | 'top-left';
  theme?: 'blue' | 'green' | 'purple' | 'orange';
  title?: string;
  subtitle?: string;
  autoOpen?: boolean;
}

const LiveChatWidget: React.FC<LiveChatWidgetProps> = ({
  apiUrl,
  socketUrl,
  tenantUrl,
  position = 'bottom-right',
  theme = 'blue',
  title = 'Chat with us!',
  subtitle = 'We typically reply in few minutes.',
  autoOpen = false
}) => {
  const [expanded, setExpanded] = useState(autoOpen);

  // Set environment variables if provided as props
  if (apiUrl) {
    (window as any).VITE_API_URL = apiUrl;
  }
  if (socketUrl) {
    (window as any).VITE_SOCKET_URL = socketUrl;
  }
  if (tenantUrl) {
    (window as any).VITE_TENANT_URL = tenantUrl;
  }

  // TODO: Implement position and theme customization
  console.log('Widget config:', { position, theme, title, subtitle });

  return (
    <div className={`live-chat-widget live-chat-widget--${position} live-chat-widget--${theme}`}>
      <ChatWidget expanded={expanded} setExpanded={setExpanded} />
      <Content expanded={expanded} setExpanded={setExpanded} />
    </div>
  );
};

export default LiveChatWidget; 