import React, { useState, useEffect, useRef } from "react";
import { Transition } from "@headlessui/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronDown,
  faEllipsisVertical,
} from "@fortawesome/free-solid-svg-icons";
import { IoMdSend } from "react-icons/io";
import axios from "axios";
import {
  REACT_APP_API_URL,
  REACT_APP_SOCKET_URL,
  REACT_APP_TENANT_URL,
} from "../../config";

interface ChatWidgetProps {
  expanded: boolean;
  setExpanded: (expanded: boolean) => void;
}

interface FormData {
  name: string;
  email: string;
  question: string;
}

interface Message {
  type: "sent" | "received";
  message: string;
  timestamp?: string;
}

interface SenderMessageProps {
  message: string;
}

interface ReceivedMessageProps {
  message: string;
}

const ChatWidget: React.FC<ChatWidgetProps> = ({ expanded, setExpanded }) => {
  const expandedFunc = () => {
    setExpanded(!expanded);
  };

  const [newMessage, setNewMessage] = useState<string>("");
  const [formData, setFormData] = useState<FormData>({
    name: "",
    email: "",
    question: "",
  });
  const [isFormSubmitted, setIsFormSubmitted] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [chatId, setChatId] = useState<string | null>(null);
  const [messages, setMessages] = useState<Message[]>([]);
  const [socket, setSocket] = useState<WebSocket | null>(null);
  const [isConnected, setIsConnected] = useState<boolean>(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const chatContainerRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: "auto" });
    }
    // Also scroll the chat container to bottom
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop =
        chatContainerRef.current.scrollHeight;
    }
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Scroll to bottom when widget is expanded
  useEffect(() => {
    if (expanded && isFormSubmitted) {
      // Small delay to ensure DOM is updated
      setTimeout(() => {
        scrollToBottom();
      }, 10);
    }
  }, [expanded, isFormSubmitted]);

  const SenderMessage: React.FC<SenderMessageProps> = ({ message }) => (
    <div className="flex flex-row-reverse items-top space-x-2 space-x-reverse space-y-6 ">
      <div className="relative px-4 py-4 bg-blue-600 rounded-2xl text-white text-left max-w-[85%]">
        {message}
      </div>
    </div>
  );

  const ReceivedMessage: React.FC<ReceivedMessageProps> = ({ message }) => (
    <div className="flex items-top space-x-2 space-y-6 ">
      <div className="relative px-4 py-4 bg-gray-200 rounded-2xl text-black text-left max-w-[85%]">
        {message}
      </div>
    </div>
  );

  const handleOnChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const message = e.target.value;
    setNewMessage(message);
  };

  const handleFormChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const connectWebSocket = (roomId: string) => {
    const wsUrl = `${REACT_APP_SOCKET_URL}/${roomId}/`;
    const newSocket = new WebSocket(wsUrl);

    newSocket.onopen = () => {
      console.log("WebSocket connected for chat widget");
      setIsConnected(true);
    };

    newSocket.onmessage = (event) => {
      const data = JSON.parse(event.data);

      if (data.command === "new_message") {
        const messageData = data.message;
        const isFromUser = messageData.name === formData.name;

        // Only add received messages (from agents/others), not sent messages
        if (!isFromUser) {
          setMessages((prev) => [
            ...prev,
            {
              type: "received",
              message: messageData.message,
              timestamp: messageData.sent,
            },
          ]);
        }
      }
    };

    newSocket.onclose = () => {
      console.log("WebSocket disconnected");
      setIsConnected(false);
    };

    newSocket.onerror = (error) => {
      console.error("WebSocket error:", error);
      setIsConnected(false);
    };

    setSocket(newSocket);
  };

  const handleFormSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsLoading(true);

    try {
      const formDataToSend = new FormData();
      formDataToSend.append("name", formData.name);
      formDataToSend.append("question", formData.question);

      const getTenantUuid = await axios.get(
        `${REACT_APP_TENANT_URL}/api/get_tenant_uuid`,
        {
          params: {
            schema_name: "ResearchBase",
          },
        }
      );

      const tenant_uuid = getTenantUuid.data.tenant_uuid;
      const response = await axios.post(
        `${REACT_APP_API_URL}/api/create_room/`,
        formDataToSend,
        {
          headers: {
            "Content-Type": "multipart/form-data",
            "X-DTS-SCHEMA": tenant_uuid,
          },
        }
      );

      if (response.data.chat_id) {
        setChatId(response.data.chat_id);
        setIsFormSubmitted(true);

        // Connect to WebSocket
        connectWebSocket(response.data.chat_id);

        // Add initial messages
        setMessages([
          { type: "received", message: "Hello! How may I help you?" },
          { type: "sent", message: formData.question },
        ]);
      }
    } catch (error) {
      console.error("Error creating chat room:", error);
      alert("Failed to start chat. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleSendMessage = async () => {
    if (!newMessage.trim() || !socket || !isConnected) return;

    // Add message to local state immediately for better UX
    setMessages((prev) => [...prev, { type: "sent", message: newMessage }]);

    // Send message via WebSocket
    socket.send(
      JSON.stringify({
        command: "new_message",
        from: formData.name,
        message: newMessage,
        chatId: chatId,
      })
    );

    setNewMessage("");
  };

  const handleKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  // Cleanup WebSocket on component unmount
  useEffect(() => {
    return () => {
      if (socket) {
        socket.close();
      }
    };
  }, [socket]);

  return (
    <Transition
      show={expanded}
      enter="transition ease-out duration-300"
      enterFrom="transform translate-y-0 scale-50"
      enterTo="transform translate-y-0 scale-100"
      leave="transition ease-in duration-75"
      leaveFrom="transform translate-y-36 scale-100"
      leaveTo="transform -translate-y-4 scale-50"
    >
      <div className="fixed right-6 bottom-4 origin-bottom-right sm:right-6 sm:bottom-4 sm:origin-bottom-right inset-0 sm:inset-auto">
        <div
          id="window"
          className="w-full h-full sm:w-[370px] sm:max-h-[700px] sm:h-auto relative -bottom-4 -right-4 sm:relative sm:bottom-4 sm:right-0 flex flex-col bg-white rounded-2xl sm:rounded-2xl rounded-none shadow-lg text"
          ref={chatContainerRef}
        >
          <div className="px-4 sm:px-6 bg-gradient-to-r from-blue-700  to-sky-400 h-32 sm:h-40 rounded-t-2xl sm:rounded-t-2xl rounded-none flex items-center justify-between shadow-lg">
            <div className="flex items-center relative space-x-3  text-neutral-100 ">
              <span className="w-12 h-12 sm:w-14 sm:h-14 bg-white rounded-full"></span>
              <span className="px-2 text-base sm:text-lg font-semibold">
                Chat with us!
              </span>
            </div>
            <div className="relative flex justify-center items-center space-x-4">
              <FontAwesomeIcon
                icon={faEllipsisVertical}
                className="hover:cursor-pointer text-white"
              />

              <FontAwesomeIcon
                title="close"
                icon={faChevronDown}
                className="w-6 h-6 text-white hover:cursor-pointer"
                onClick={expandedFunc}
              />
            </div>
          </div>
          <div className="relative bg-gradient-to-r from-blue-700 to-sky-400 text-white px-4 sm:px-6 pt-3 sm:pt-4 pb-4 sm:pb-6 border-t-[1px] border-t-white">
            <svg
              className="absolute -bottom-0 left-0 w-full"
              viewBox="0 0 1440 100"
              preserveAspectRatio="none"
            >
              <path
                d="M-1 50C463 149 739-50 1441 56L1443 113-1 111Z"
                fill="white"
              />
            </svg>
            <p className="text-left text-sm sm:text-base font-medium">
              We typically reply in few minutes.
            </p>
          </div>

          {!isFormSubmitted ? (
            // Form Section
            <div className="flex flex-col p-4 sm:p-6 space-y-4 flex-1">
              <h3 className="text-lg font-semibold text-gray-800">
                Start a conversation
              </h3>
              <form onSubmit={handleFormSubmit} className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Name *
                  </label>
                  <input
                    type="text"
                    name="name"
                    value={formData.name}
                    onChange={handleFormChange}
                    required
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    placeholder="Enter your name"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Email *
                  </label>
                  <input
                    type="email"
                    name="email"
                    value={formData.email}
                    onChange={handleFormChange}
                    required
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    placeholder="Enter your email"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    How can we help you? *
                  </label>
                  <textarea
                    name="question"
                    value={formData.question}
                    onChange={handleFormChange}
                    required
                    rows={3}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none"
                    placeholder="Describe your question or issue..."
                  />
                </div>

                <button
                  type="submit"
                  disabled={isLoading}
                  className="w-full bg-blue-600 text-white py-2 px-4 rounded-lg hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                >
                  {isLoading ? "Starting chat..." : "Start Chat"}
                </button>
              </form>
            </div>
          ) : (
            // Chat Section
            <>
              <div
                ref={chatContainerRef}
                className="flex flex-col overflow-y-auto py-4 px-4 sm:px-6 space-y-3 text-sm flex-1 max-h-[calc(100vh-200px)] sm:max-h-[400px]"
              >
                {messages.map((msg, index) =>
                  msg.type === "sent" ? (
                    <SenderMessage key={index} message={msg.message} />
                  ) : (
                    <ReceivedMessage key={index} message={msg.message} />
                  )
                )}
                <div ref={messagesEndRef} />
              </div>

              <div className="flex flex-col items-center px-4">
                <div className="flex justify-between items-center w-full border-t-[1px]">
                  <input
                    type="text"
                    placeholder={
                      isConnected ? "Enter your message..." : "Connecting..."
                    }
                    value={newMessage}
                    onChange={handleOnChange}
                    onKeyPress={handleKeyPress}
                    disabled={!isConnected}
                    className="relative w-full h-12 rounded-lg px-2 outline-none border-none focus:border-none focus:outline-hidden focus:ring-0 disabled:opacity-50"
                  />
                  <IoMdSend
                    title="Send"
                    className={`w-6 h-6 cursor-pointer ${
                      newMessage.length && isConnected
                        ? "text-blue-700"
                        : "text-gray-200"
                    }`}
                    onClick={handleSendMessage}
                  />
                </div>
                {!isConnected && (
                  <div className="text-xs text-gray-500 mt-1">
                    Connecting to chat...
                  </div>
                )}
              </div>
            </>
          )}
        </div>
      </div>
    </Transition>
  );
};

export default ChatWidget;
