import React from "react";
import { Transition } from "@headlessui/react";

interface ContentProps {
  expanded: boolean;
  setExpanded: (expanded: boolean) => void;
}

const Content: React.FC<ContentProps> = ({ expanded, setExpanded }) => {
  return (
    <div className="absolute">
      <Transition
        show={!expanded}
        enter="transition ease-out duration-200"
        enterFrom="transform translate-y-12"
        enterTo="transform translate-y-0"
        leave="transition ease-in duration-200"
        leaveFrom="transform translate-y-0"
        leaveTo="transform translate-y-36"
      >
        {/* Use absolute positioning instead of fixed, so it's relative to the shadow root */}
        <div className="absolute right-4 bottom-2 md:right-4 md:bottom-2 origin-bottom">
          <div
            className="relative right-6 bottom-4 md:right-6 md:bottom-4 p-3 md:p-3 text-center bg-blue-500 shadow-lg rounded-full hover:cursor-pointer hover:bg-blue-600"
            onClick={() => {
              setExpanded(!expanded);
            }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-8 w-8 md:h-10 md:w-10"
              viewBox="0 0 20 20"
              fill="white"
            >
              <path
                fillRule="evenodd"
                d="M18 10c0 3.866-3.582 7-8 7a8.841 8.841 0 01-4.083-.98L2 17l1.338-3.123C2.493 12.767 2 11.434 2 10c0-3.866 3.582-7 8-7s8 3.134 8 7zM7 9H5v2h2V9zm8 0h-2v2h2V9zM9 9h2v2H9V9z"
                clipRule="evenodd"
              />
            </svg>
          </div>
        </div>
      </Transition>
    </div>
  );
};

export default Content;
